import { buildModel } from '@/model/buildModel.js'

export default {
  data () {
    return {
      actId: '',
      infoData: {}
    }
  },
  onLoad (params = {}) {
    if (params.type === 'skill') {
      uni.setNavigationBarTitle({
        title: '拓客技巧详情'
      })
    }
    if (params.type === 'point') {
      uni.setNavigationBarTitle({
        title: '楼盘卖点详情'
      })
    }
    this.actId = params.actId
    this.initData()
  },
  methods: {
    initData () {
      let obj = new buildModel().getModel('saveSkillPointDetail') || {}
      this.infoData = obj
    }
  },
}
